import { NgModule, ErrorHandler } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { IonicApp, IonicModule, IonicErrorHandler } from 'ionic-angular';
import { MyApp } from './app.component';
import { MyProfilePage } from '../pages/my-profile/my-profile';
import { MyPaymentsPage } from '../pages/my-payments/my-payments';
import { MyDeliveriesPage } from '../pages/my-deliveries/my-deliveries';
import { LoginPage } from '../pages/login/login';
import { MyOrdersPage } from '../pages/my-orders/my-orders';
import { HomePagePage } from '../pages/home-page/home-page';
import { PreviousPurchasesPage } from '../pages/previous-purchases/previous-purchases';
import { PendingOrdersPage } from '../pages/pending-orders/pending-orders';
import { RecordDepositPage } from '../pages/record-deposit/record-deposit';
import { OrderPage } from '../pages/order/order';
import { DeliveryPage } from '../pages/delivery/delivery';
import { CalculatePaymentPage } from '../pages/calculate-payment/calculate-payment';
import { UploadDepositSlipPage } from '../pages/upload-deposit-slip/upload-deposit-slip';
import { EditProfile } from '../pages/Edit-profile/Edit-profile';

import { WarrantyHomePage } from '../pages/warranty-home/warranty-home';
import { SubmitClaimNamePage } from '../pages/submit-claim-name/submit-claim-name';
import { TrackClaimPage } from '../pages/track-claim/track-claim';
import { PreviousClaimsPage } from '../pages/previous-claims/previous-claims';
import { WarrantyOrderListPage } from '../pages/warranty-order-list/warranty-order-list';
import { ClaimOrderPage } from '../pages/claim-order/claim-order';
import { WarrantyDetailsPage } from '../pages/warranty-details/warranty-details';
import { SubmitRequestPage } from '../pages/submit-request/submit-request';
import { TrackRequestIdPage } from '../pages/track-request-id/track-request-id';
import { TrackListPage } from '../pages/track-list/track-list';
import { TrackingStatusPage } from '../pages/tracking-status/tracking-status';
import { TrackingStatusListPage } from '../pages/tracking-status-list/tracking-status-list';
import { PersonalOrderPage } from '../pages/personal-orders/personal-orders';
import { CustomerOrder } from '../pages/customer-orders/customer-orders';
import { WithinWarrantyPeriodPage } from '../pages/within-warranty-period/within-warranty-period';


import { Http,HttpModule } from '@angular/http';


import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';

@NgModule({
  declarations: [
    MyApp,
    MyProfilePage,
    MyPaymentsPage,
    MyDeliveriesPage,
    LoginPage,
    MyOrdersPage,
    HomePagePage,
    PreviousPurchasesPage,
    PendingOrdersPage,
    RecordDepositPage,
    OrderPage,
    DeliveryPage,
    CalculatePaymentPage,
    UploadDepositSlipPage,
    EditProfile,
    PersonalOrderPage,
    CustomerOrder,
    WarrantyHomePage,
    SubmitClaimNamePage,
    TrackClaimPage,
    PreviousClaimsPage,
    WarrantyOrderListPage,
    ClaimOrderPage,
    WarrantyDetailsPage,
    SubmitRequestPage,
    TrackRequestIdPage,
    TrackListPage,
    TrackingStatusPage,
    TrackingStatusListPage,
    WithinWarrantyPeriodPage
  ],
  imports: [
    BrowserModule,
    HttpModule,
    IonicModule.forRoot(MyApp, {}, 

{
  links: [
    { loadChildren: '../pages/submit-request/submit-request.module#SubmitRequestPageModule', name: 'SubmitRequestPage', segment: 'submit-request', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/within-warranty-period/within-warranty-period.module#WithinWarrantyPeriodPageModule', name: 'WithinWarrantyPeriodPage', segment: 'within-warranty-period', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/warranty-details/warranty-details.module#WarrantyDetailsPageModule', name: 'WarrantyDetailsPage', segment: 'warranty-details', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/claim-order/claim-order.module#ClaimOrderPageModule', name: 'ClaimOrderPage', segment: 'claim-order', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/warranty-order-list/warranty-order-list.module#WarrantyOrderListPageModule', name: 'WarrantyOrderListPage', segment: 'warranty-order-list', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/previous-claims/previous-claims.module#PreviousClaimsPageModule', name: 'PreviousClaimsPage', segment: 'previous-claims', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/submit-claim-name/submit-claim-name.module#SubmitClaimNamePageModule', name: 'SubmitClaimNamePage', segment: 'submit-claim-name', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/tracking-status/tracking-status.module#TrackingStatusPageModule', name: 'TrackingStatusPage', segment: 'tracking-status', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/track-request-id/track-request-id.module#TrackRequestIdPageModule', name: 'TrackRequestIdPage', segment: 'track-request-id', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/tracking-status-list/tracking-status-list.module#TrackingStatusListPageModule', name: 'TrackingStatusListPage', segment: 'tracking-status-list', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/track-list/track-list.module#TrackListPageModule', name: 'TrackListPage', segment: 'track-list', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/track-claim/track-claim.module#TrackClaimPageModule', name: 'TrackClaimPage', segment: 'track-claim', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/warranty-home/warranty-home.module#WarrantyHomePageModule', name: 'WarrantyHomePage', segment: 'warranty-home', priority: 'low', defaultHistory: [] }
  ]
})
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    MyProfilePage,
    MyPaymentsPage,
    MyDeliveriesPage,
    LoginPage,
    MyOrdersPage,
    HomePagePage,
    PreviousPurchasesPage,
    PendingOrdersPage,
    RecordDepositPage,
    OrderPage,
    DeliveryPage,
    CalculatePaymentPage,
    UploadDepositSlipPage,
    EditProfile,
    WarrantyHomePage,
    SubmitClaimNamePage,
    TrackClaimPage,
    PreviousClaimsPage,
    WarrantyOrderListPage,
    ClaimOrderPage,
    CustomerOrder,
    PersonalOrderPage,
    WarrantyDetailsPage,
    SubmitRequestPage,
    TrackRequestIdPage,
    TrackListPage,
    TrackingStatusPage,
    TrackingStatusListPage,
    WithinWarrantyPeriodPage
  ],
  providers: [
    StatusBar,
    SplashScreen,
    {provide: ErrorHandler, useClass: IonicErrorHandler},
  ]
})
export class AppModule {}


